import React from "react";
import {Button, Form, Table, Header, Segment} from "semantic-ui-react";


const TypeComponent = (type) => {
    let properties = (type.properties || []).map(p => (
        <Table.Row>
            <Table.Cell> <Form.Input name="name" defaultValue={p.name} placeholder="name"/>
            </Table.Cell>
            <Table.Cell><Form.Input name="type" defaultValue={p.type} placeholder="type"/></Table.Cell>
            <Table.Cell>None</Table.Cell>
            <Table.Cell><Form.TextArea name='description' placeholder='description'
                                       rows='3'/></Table.Cell>
        </Table.Row>
    ))
    return (
        <div>
            <Header as='h3' attached='top'>
                Type
            </Header>
            <Segment attached>

                <Form onSubmit={(e, {formData}) => {
                    e.preventDefault()
                    console.log(formData)
                }}>

                    <Form.Group widths='2'>
                        <Form.Field>
                            <Form.Input label='Type' name='type' defaultValue={type.name} placeholder='Type'/>
                        </Form.Field>
                        <Form.Field>
                            <Form.Input label='Namespace' name='namespace' defaultValue={type.namespace}
                                        placeholder='Type namespace'/>
                        </Form.Field>
                    </Form.Group> </Form>
            </Segment>

            <Header as='h3' attached>
                Properties
            </Header>
            <Segment attached>
                <Form onSubmit={(e, {formData}) => {
                    e.preventDefault()
                    console.log(formData)
                }}>
                    <Table celled>
                        <Table.Header>
                            <Table.Row>
                                <Table.HeaderCell>Name</Table.HeaderCell>
                                <Table.HeaderCell>Type</Table.HeaderCell>
                                <Table.HeaderCell>Constraints</Table.HeaderCell>
                                <Table.HeaderCell>Description</Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>

                        <Table.Body>
                            {properties}
                        </Table.Body>
                    </Table>

                    <Button primary type='submit'>Submit</Button>
                </Form></Segment>
        </div>
    )
}


export default TypeComponent


